import httpUrl from '@/common/config.js'
import { mapState, mapActions } from 'vuex'
export default {
	data() {
		return {
			goodsInfo: {
				name: '',
				price: '',
				attribute: [],
				image: '',
				type:''
			},
			goodsImg: '/static/plus.png',
			percent: 0,
			isUpload: false,
			domainName:httpUrl,
			url_config:''
		}
	},
	computed:{
		...mapState(['handleGoods'])
	},
	onLoad(){
		this.url_config = getApp().globalData.url_config
		this.goodsInfo={
			...this.goodsInfo,
			...this.handleGoods
		}
		console.log(this.goodsInfo)
		console.log(getApp().globalData.url_config)
	},
	methods: {
		...mapActions(['updateGoods']),
		async formSubmit() {
			console.log(this.goodsInfo)
			let res = await this.updateGoods({
				params:this.goodsInfo
			})
			console.log('来了',res)
		},
		formReset() {

		},
		addAttr() {
			this.goodsInfo.attribute.push({
				type: '',
				valueArr: []
			})
		},
		chooseImg() {
			uni.chooseImage({
				count: 1, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album'], //从相册选择
				success: (res) => {
					console.log(res);
					this.goodsImg = res.tempFilePaths
					const tempFilePaths = res.tempFilePaths;

					this.isUpload = true
					const uploadTask = uni.uploadFile({
						url: httpUrl+'/file/upload',
						filePath: tempFilePaths[0],
						name: 'file',
						formData: {},
						success: (uploadFileRes) => {
							let data = JSON.parse(uploadFileRes.data)
							console.log(data);
							this.goodsInfo.image = data.filePath
						},
						complete: () => {
							this.isUpload = false
							this.percent = 0
						}
					});
					uploadTask.onProgressUpdate((res) => {
						this.percent = res.progress;
						// console.log('上传进度' + res.progress);
						// console.log('已经上传的数据长度' + res.totalBytesSent);
						// console.log('预期需要上传的数据总长度' + res.totalBytesExpectedToSend);
					});
				}
			});
		}
	}
}
